<template>
	<div class="index-board">
		<div class="inner userIdent-inner">
			<h3>个人信息认证</h3>
			<div>
				 <Form :model="userIdent" label-position="left" :label-width="80" inline>
					<FormItem label="名">
						<Input v-model="userIdent.Name" placeholder="请输入名"></Input>
					</FormItem>
					<FormItem label="姓">
						<Input v-model="userIdent.Surname" placeholder="请输入姓"></Input>
					</FormItem>
					<FormItem label="性别">
						<Input v-model="userIdent.Sex" placeholder="请输入性别"></Input>
					</FormItem>
					<FormItem label="国籍">
						<Input v-model="userIdent.nationality" placeholder="请输入国籍"></Input>
					</FormItem>
					<FormItem label="出生日期">
						<DatePicker style="width:100%" type="date" placeholder="出生日期" v-model="userIdent.BirDate"></DatePicker>
					</FormItem>
					<br>
					<FormItem label="Address" class="address">
						<Input v-model="userIdent.Address" readonly></Input>
					</FormItem>
					<FormItem label="护照上传">
						<Upload action="//jsonplaceholder.typicode.com/posts/" accept="image/jpeg,image/jpg,image/png">
							<Button icon="ios-cloud-upload-outline">Upload files</Button>
						</Upload>
					</FormItem>
				</Form>
				<div class="btn-con">
				    <Button type="primary" @click="backConfig()">
						<Icon type="ios-arrow-back"></Icon>
						上一步
					</Button>
					<Button type="primary" @click="goCompany()">
						下一步
						<Icon type="ios-arrow-forward"></Icon>
					</Button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
				userIdent: {
                    Name: '',
                    Surname: '',
                    Sex: '',
					nationality:'',
					BirDate:'',
					Address:''
                },
			}
		},
		methods:{
			backConfig(){
				this.$router.push({path:'Config'})
			},
			goCompany(){
				this.$router.push({path:'CompanyIdent'})
			}
		}
	}
</script>

<style scoped lang="stylus">
	.index-board
		padding: 50px 100px !important;
		height: 550px;
		h3
			font-size:26px;
			margin-bottom: 20px;
		.ivu-form-item
			min-width: 330px;
			.ivu-form-item-label
				font-size: 16px;
		.address
			width: 96%;
		.btn-con
			display: flex;
			display: -webkit-flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 50px;
</style>


